import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

import { Events } from 'ionic-angular';
import { Storage } from '@ionic/storage';

import { BaseSettingsProvider } from '../base-settings/base-settings';

@Injectable()
export class UserProvider {
  public api_url: string;
  public users_api_url: string;

  /**
   * Initialize
   */
  constructor(public http: Http, public base: BaseSettingsProvider, public storage: Storage, public events: Events) {
    // Generate the API URL
    this.api_url = base.url + '/auth';
  }

  /**
   * Get Targeted Listing
   */
  login(data: object) {
    let url = this.api_url + '/login';
    return this.http.post(url, data).map(res => res.json());
  }

  /**
   * Check User Token Validity
   */
  checkToken(data: object) {
    let url = this.api_url + '/check-token';
    return this.http.post(url, data).map(res => res.json());
  }
}



