import { Component } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { IonicPage, NavController, NavParams, LoadingController, ToastController, Events } from 'ionic-angular';

import { UserProvider } from '../../providers/user/user';
import { IonicStorageProvider } from '../../providers/base-settings/storage';

@IonicPage()
@Component({
  selector: 'page-registration',
  templateUrl: 'registration.html'
})
export class RegistrationPage {
  public registrationForm: FormGroup;

  /**
   * Constructor Method
   */
  constructor(public navCtrl: NavController, public navParams: NavParams, private formBuilder: FormBuilder,
    private loadingCtrl: LoadingController, private toastCtrl: ToastController, private userProvider: UserProvider,
    private ionStorage: IonicStorageProvider, private event: Events) {
    this.registrationForm = this.formBuilder.group({
      name: ['', Validators.required],
      username: ['', Validators.required],
      password: ['', Validators.required],
      confirmpassword: ['', Validators.required]
    });
  }

  /**
   * Logic to Check User
   */
  registration() {
    let formdata = this.registrationForm.value;
    console.log(formdata);

    let params = {
      "name": formdata.name,
      "username": formdata.username,
      "password": formdata.password
    };

    /**
     * Get Locally Stored Session Data using Storage Provider
     */
    this.ionStorage.getItems('sessionusers').then(data => {
      console.log(data);
      let susers = [];
      if(data && data != null) {
        susers = data;
      }

      susers.push(params);
      this.ionStorage.setItem('sessionusers', susers);
      this.gotoLoginPage();
    });
  }

  /**
   * Go to Login Page
   */
  gotoLoginPage() {
    this.navCtrl.setRoot('LoginPage');
  }

  /**
   * Go to Dashboard Page
   */
  gotoDashboardPage() {
    this.navCtrl.setRoot('DashboardPage');
  }
}



